@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;400;700&display=swap);
.home{width:100vw;padding:0px 6vw;display:flex;flex-direction:row;justify-content:center;grid-column-gap:14px;-webkit-column-gap:14px;column-gap:14px}.home__profile{max-height:320px;width:220px;position:-webkit-sticky;position:sticky;top:80px;background-color:#f6f5f5;border-radius:20px;overflow:hidden}.home__newsfeed{width:50%;min-height:100vh}.home__honor{max-height:500px;width:330px;padding:10px;position:-webkit-sticky;position:sticky;top:80px;background-color:#f6f5f5;border-radius:20px}.profile__info{width:100%;height:220px;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.profile__info__image{width:100px;height:100px;background-color:black;border-radius:50%;overflow:hidden}.profile__info__image>img{width:100%;height:100%;object-fit:cover}.profile__info__container{display:flex;flex-direction:column;justify-content:center;align-items:center}.profile__info__container__name{font-size:1rem}.profile__info__container__major{font-size:0.8rem}.profile__follow{width:100%;height:100px;padding:12px;border-top:solid 5px #d3e0ea;display:flex;flex-direction:column;justify-content:center;align-items:flex-start;font-size:0.9rem}.profile__follow__follower{width:100%;display:flex;justify-content:space-between}.profile__follow__follower--number{color:#276678;font-weight:bold}.profile__follow__navigate{cursor:pointer;font-weight:bold;transition:all 0.5s}.profile__follow__navigate:hover{color:#276678}.honor__text{height:8%}.honor__list{height:92%;overflow-y:scroll}.honorProfile{width:100%;display:flex;flex-direction:row;justify-content:space-between;align-items:center;margin-bottom:12px}.honorProfile__img{width:50px;height:50px;overflow:hidden;border-radius:50%}.honorProfile__img>img{width:100%;height:100%;object-fit:cover}.honorProfile__container{height:100%;display:flex;flex-direction:column;justify-items:center}.honorProfile__container__name{font-size:0.9rem}.honorProfile__container__info{font-size:0.6rem}.honorProfile__btn{width:70px;height:40px;display:flex;justify-content:center;align-items:center;border-radius:40px;border:solid 1px #1687a7;font-size:0.7rem;transition:all 0.5s}.honorProfile__btn:hover{background-color:#1687a7;color:#f6f5f5}.newsfeed__content{min-height:200px;margin-bottom:20px;display:flex;flex-direction:column;grid-gap:10px;gap:10px;padding:10px;border-radius:20px;background-color:#f6f5f5}.newsfeed__content__writer{display:flex;flex-direction:row;grid-gap:10px;gap:10px}.newsfeed__content__writer__img{width:50px;height:50px;border-radius:50%;overflow:hidden}.newsfeed__content__writer__img>img{width:100%;height:100%;object-fit:cover}.newsfeed__content__writer__info{display:flex;flex-direction:column}.newsfeed__content__writer__info--major{font-size:0.8rem}

.nav{width:100%;height:70px;background-color:#f6f5f5;padding:0px 6vw;z-index:99;position:-webkit-sticky;position:sticky;left:0;top:0;display:flex;justify-content:space-between;align-items:center}.nav a{all:unset;color:black;cursor:pointer}.nav__right{display:flex;justify-content:space-between;align-items:center}.nav__right>*{margin-right:6px}.nav__right__logo--emph{color:#276678}.nav__category{height:100%;display:flex;justify-content:space-between;align-items:center}.nav__category__element{display:flex;flex-direction:column;justify-content:space-between;align-items:center;width:6vw;height:100%;min-width:4vw}.nav__right__search input{all:unset;height:36px;width:100%;background-color:#d3e0ea;border-radius:12px;padding-left:10px;transition:width 0.3s}.nav__right__search input:focus{width:26vw}.nav__category__element__border{height:3px;width:100%;background-color:#276678;transform:scaleX(0);transform-origin:left;transition:transform 0.3s}.nav__category__element__border--selected{transform:scaleX(1)}.nav__dropdown__border{height:3px;width:100%;background-color:#276678;transform:scaleX(0);transform-origin:left;transition:transform 0.3s}.nav__dropdown:hover .nav__dropdown__border{transform:scaleX(1)}.nav__dropdown:hover .nav__dropdown--content{visibility:visible;opacity:1;transform:translateX(0%)}.nav__dropdown--content{visibility:hidden;position:absolute;opacity:0;top:110%;transform:translateX(10%);background-color:#f6f5f5;transition:all 0.5s;width:10vw;height:150px;padding:6px 12px;display:flex;flex-direction:column;justify-content:space-evenly;grid-row-gap:20px;row-gap:20px;box-shadow:0 19px 38px rgba(0,0,0,0.2),0 15px 12px rgba(0,0,0,0.1)}.nav__dropdown--content>a{transition:color 0.5s}.nav__dropdown--content>a:hover{color:#1687a7}

.center{width:100vw;height:90vh;position:absolute;display:flex;flex-direction:column;justify-content:center;align-items:center}.signinform{width:500px;height:500px;padding:20px;background-color:#f6f5f5;box-shadow:0 19px 38px rgba(0,0,0,0.2),0 15px 12px rgba(0,0,0,0.1);display:flex;flex-direction:column;justify-content:center;align-items:center}.signinform>*{margin-bottom:20px}.signinform__social{width:100%;display:flex;justify-content:center;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px}.signinform__social>svg{color:#d3e0ea;transform:scale(0.8);transition:all 0.5s}.signinform__social>svg:hover{color:#1687a7;transform:scale(1)}.signinform__link{align-self:flex-end;font-size:0.8rem}.signinform__link>a{all:unset;font-weight:bold;color:#1687a7;cursor:pointer}.signinform>button{all:unset;display:flex;justify-content:center;align-items:center;width:80%;height:60px;border-radius:12px;cursor:pointer;background-color:#d3e0ea;transition:all .5s}.signinform>button:hover{background-color:#1687a7;color:#f6f5f5}.signinform__input{position:relative;display:flex;justify-content:center;align-items:center;width:100%;height:60px;margin-bottom:20px}.signinform__input__placeholder{position:absolute;bottom:50%;left:10%;transition:all 0.4s}.signinform__input__tag{all:unset;padding-left:4%;width:80%;height:100%;border-bottom:solid 4px #d3e0ea;transition:all 0.5s}.signinform__input__tag:focus{border-bottom:solid 4px #1687a7}.signinform__input__tag:focus+.signinform__input__placeholder,.signinform__input__tag:valid+.signinform__input__placeholder{font-size:0.8rem;bottom:90%}.signupform{height:auto}

*{margin:0;padding:0;box-sizing:border-box;font-family:"Noto Sans KR", sans-serif}.page{width:100vw;min-height:100vh;background-color:#d3e0ea;padding-top:10px}.slide-enter,.slide-exit{transition:all 1000ms ease-in-out}.slide-enter{position:absolute;opacity:0;transform:translateX(20%)}.slide-enter.slide-enter-active{opacity:1;transform:translateX(0)}.slide-exit{opacity:1;transform:translateX(0%)}.slide-exit-active{opacity:0;transform:translateX(-20%)}

